.nav_link, .active {
  display: block;
  text-decoration: none;
  font-family: 'Rowdies';
  font-size: 22px;
  font-weight: 500;
  color: rgb(91, 7, 117);
  transition: all 0.7s ease 0s;
  margin-bottom: 20px;
  &:nth-last-child(-n+2), &:last-child {
    margin-bottom: 0;
    margin-top: 50px;
  }
  &:hover {
    filter: brightness(1.3);
    font-size: 30px;
  }
}
.active {
  color: rgb(203, 20, 210);
  font-size: 30px;
  &:hover {
    font-size: 30px;
  }
}

