.wrapper {
  background: linear-gradient(45deg, rgb(14, 21, 56) 68%, rgb(43, 60, 144));
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
}
.logo {
  margin: 5px 0 0 20px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  & img {
    max-width: 100%;
    max-height: 100%;
  }
}
.login_block {
  display: flex;
  align-items: center;

}
.loggedUser {
  margin: 0 30px 0 0;
  font-size: 1.5em;
  font-weight: bold;
  color:#00A157;
}
.button{
  display: block;
  width: 100px;
  height: 30px;
  font-size: 16px;
  margin: 0 0 0 20px;
  & >button{
    text-transform: uppercase;
  }
}