
.button {
  margin: 0 0 10px 0;
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
  font-size: 1em;
  text-decoration: none;
  background-color: #FFF;
  border: none;
  & > span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0e1538;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 1em;
    font-weight: bold;
    letter-spacing: 2px;
    border: 1px solid #040a29;
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: -50%;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.075);
      transform: skew(25deg);
    }
  }
  &:before, &:after {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(45deg, #ff075b, #0e1538, #0e1538, #1aff22);
    transition: 0.5s;
  }
  &:hover {
    &:before {
      inset: -3px;
    }
    &:after {
      inset: -3px;
      filter: blur(10px);
    }
  }
}
