.wrapper {
  min-height: 100vh;
  padding: 5px;
  display: grid;
  grid-template-rows: 70px 1fr;
  grid-template-columns: 3fr 10fr;
  grid-gap: 5px;
  grid-template-areas:
        'header header'
        'sidebar content';
  & :nth-child(1) {
    grid-area: header;
  }
  & :nth-child(2) {
    grid-area: sidebar;
  }
  &:nth-child(3) {
    grid-area: content;
  }
}
.content {
  width: 100%;
  background: linear-gradient(0deg, #30cfd0, #330867);
  border-radius: 0 10px 10px 0;
  overflow: hidden;
}

