.wrapper{
  margin: 10px;

}
.frend{
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  & span{
    margin: 0 0 5px 0;
    font-size: 12px;
    font-weight: bold;
  }
  & img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    overflow: hidden;
  }
}